<template>
  <!-- CSV‰∏ä‰º†Âç°ÁâáÁªÑ‰ª∂ -->
  <el-card class="uploader-card">
    <!-- Âç°ÁâáÂ§¥ÈÉ®Âå∫Âüü -->
    <template #header>
      <div class="card-header">
        <span>üìÅ ‰∏ä‰º†CSVÊñá‰ª∂</span>
      </div>
    </template>

    <!-- ÊãñÊãΩ‰∏ä‰º†Âå∫Âüü -->
    <div
      class="upload-area"
      @dragover.prevent="handleDragOver"
      @dragleave="handleDragLeave"
      @drop.prevent="handleDrop"
      :class="{ dragging: isDragging }"
      @click="triggerFileInput"
    >
      <div class="upload-content">
        <i class="el-icon-upload" style="font-size: 48px; color: #409eff"></i>
        <p class="upload-text">{{ uploadText }}</p>
        <p class="upload-hint">ÊîØÊåÅ .csvÊ†ºÂºè</p>
      </div>
    </div>

    <!-- ÈöêËóèÁöÑÊñá‰ª∂ËæìÂÖ•Ê°Ü -->
    <input
      ref="fileInput"
      type="file"
      accept=".csv"
      @change="handleFileSelect"
      style="display: none"
    />

    <!-- Êìç‰ΩúÊåâÈíÆÂå∫Âüü -->
    <div v-if="currentFile && !isParsing" class="action-section">
      <div class="action-buttons">
        <el-button
          type="primary"
          @click="parseCSVFile"
          :loading="isParsing"
          icon="el-icon-document-checked"
        >
          {{ isParsing ? "Ëß£Êûê‰∏≠..." : "Ëß£ÊûêÊñá‰ª∂" }}
        </el-button>
        <el-button
          @click="clearFile"
          :disabled="isParsing"
          icon="el-icon-delete"
        >
          Ê∏ÖÈô§
        </el-button>
      </div>
    </div>

    <!-- Êñá‰ª∂‰ø°ÊÅØÊòæÁ§∫Âå∫Âüü -->
    <div v-if="currentFile" class="file-info">
      <el-alert :title="fileInfo" type="info" show-icon :closable="false" />
    </div>

    <!-- Ëß£ÊûêÁä∂ÊÄÅÊòæÁ§∫Âå∫Âüü -->
    <div v-if="isParsing" class="parse-status">
      <el-alert :title="parseStatus" type="info" :closable="false" show-icon />
      <div class="progress-container">
        <div class="progress-text">
          <i class="el-icon-loading" style="margin-right: 8px"></i>
          {{ "Ê≠£Âú®Â§ÑÁêÜÔºåËØ∑Á®çÂÄô..." }}
        </div>
      </div>
    </div>

    <!-- Ëß£ÊûêÈîôËØØÊèêÁ§∫Âå∫Âüü -->
    <div v-if="parseError" class="error-message">
      <el-alert
        :title="errorTitle"
        type="error"
        show-icon
        :description="errorDescription"
        @close="clearError"
      >
        <template v-if="showErrorDetails">
          <div class="error-details">
            <p><strong>ÈîôËØØËØ¶ÊÉÖÔºö</strong></p>
            <pre class="error-detail-content">{{ parseErrorDetails }}</pre>
          </div>
          <div class="error-solution">
            <p><strong>Ëß£ÂÜ≥ÊñπÊ°àÔºö</strong></p>
            <ul>
              <li v-for="solution in errorSolutions" :key="solution">
                {{ solution }}
              </li>
            </ul>
          </div>
        </template>
      </el-alert>
    </div>

    <!-- ÊµèËßàÂô®ÂÖºÂÆπÊÄßÊèêÁ§∫Âå∫Âüü -->
    <div v-if="browserInfo.showWarning" class="browser-warning">
      <el-alert
        type="warning"
        show-icon
        :closable="true"
        @close="browserInfo.showWarning = false"
      >
        <div slot="title">
          <i class="el-icon-warning" style="margin-right: 8px"></i>
          ÊµèËßàÂô®ÂÖºÂÆπÊÄßÊèêÁ§∫
        </div>
        <p>
          Ê£ÄÊµãÂà∞ÊÇ®Ê≠£Âú®‰ΩøÁî® {{ browserInfo.name }}
          {{ browserInfo.version }}ÔºåÂèØËÉΩÂ≠òÂú®ÂÖºÂÆπÊÄßÈóÆÈ¢ò
        </p>
        <p>Âª∫ËÆÆ‰ΩøÁî® Chrome„ÄÅFirefox„ÄÅEdge Á≠âÁé∞‰ª£ÊµèËßàÂô®Ëé∑ÂæóÊúÄ‰Ω≥‰ΩìÈ™å</p>
      </el-alert>
    </div>
  </el-card>
</template>

<script>
// ÂØºÂÖ•PapaParseÂ∫ìÁî®‰∫éCSVËß£Êûê
import Papa from "papaparse";

export default {
  name: "CsvUploader",

  data() {
    return {
      // Êñá‰ª∂Áõ∏ÂÖ≥
      currentFile: null,

      // Áä∂ÊÄÅÁõ∏ÂÖ≥
      isDragging: false,
      isParsing: false,
      parseStatus: "Á≠âÂæÖËß£Êûê...",
      parseError: "",
      parseErrorDetails: "",
      errorSolutions: [],

      // Ëß£ÊûêÈÖçÁΩÆ
      parseConfig: {
        header: true,
        dynamicTyping: true,
        skipEmptyLines: true,
        worker: false,
        encoding: "UTF-8",
        delimitersToGuess: [",", ";", "\t", "|"],
        preview: 0,
        chunk: null,
        fastMode: false,
      },

      // ÊµèËßàÂô®Ê£ÄÊµã
      browserInfo: {
        name: "",
        version: "",
        isCompatible: true,
        showWarning: false,
      },
    };
  },

  computed: {
    // ‰∏ä‰º†Âå∫ÂüüÊòæÁ§∫ÁöÑÊñáÊú¨
    uploadText() {
      if (this.isDragging) return "ÈáäÊîæÊñá‰ª∂";
      if (this.currentFile) return "Â∑≤ÈÄâÊã©Êñá‰ª∂ÔºåÁÇπÂáªÈáçÊñ∞ÈÄâÊã©ÊàñÊãñÊãΩÊñ∞Êñá‰ª∂";
      return "ÁÇπÂáªÊàñÊãñÊãΩÊñá‰ª∂Âà∞Ê≠§Â§Ñ";
    },

    // Êñá‰ª∂‰ø°ÊÅØÊòæÁ§∫
    fileInfo() {
      if (!this.currentFile) return "";
      const sizeMB = (this.currentFile.size / 1024 / 1024).toFixed(2);
      return `Â∑≤ÈÄâÊã©Êñá‰ª∂: ${this.currentFile.name} (${sizeMB} MB)`;
    },

    // ÈîôËØØÊ†áÈ¢ò
    errorTitle() {
      if (this.parseError.includes("ÁºñÁ†Å")) {
        return "Êñá‰ª∂ÁºñÁ†ÅÈîôËØØ";
      } else if (this.parseError.includes("Ê†ºÂºè")) {
        return "Êñá‰ª∂Ê†ºÂºèÈîôËØØ";
      } else if (this.parseError.includes("Â§ßÂ∞è")) {
        return "Êñá‰ª∂ËøáÂ§ß";
      } else if (this.parseError.includes("ËØªÂèñ")) {
        return "Êñá‰ª∂ËØªÂèñÂ§±Ë¥•";
      }
      return "Ëß£ÊûêÂ§±Ë¥•";
    },

    // ÈîôËØØÊèèËø∞
    errorDescription() {
      if (this.parseError.includes("360")) {
        return "Ê£ÄÊµãÂà∞360ÊµèËßàÂô®ÂÖºÂÆπÊÄßÈôêÂà∂";
      }
      return this.parseError;
    },

    // ÊòØÂê¶ÊòæÁ§∫ÈîôËØØËØ¶ÊÉÖ
    showErrorDetails() {
      return this.parseErrorDetails && this.parseErrorDetails.length > 0;
    },
  },

  mounted() {
    // ÁªÑ‰ª∂ÊåÇËΩΩÊó∂Ê£ÄÊµãÊµèËßàÂô®ÂÖºÂÆπÊÄß
    this.detectBrowser();
  },

  methods: {
    /**
     * Ê£ÄÊµãÊµèËßàÂô®ÂÖºÂÆπÊÄß
     */
    detectBrowser() {
      const ua = navigator.userAgent.toLowerCase();
      let browserName = "Êú™Áü•ÊµèËßàÂô®";
      let browserVersion = "";
      let isCompatible = true;

      // Ê£ÄÊµã‰∏ªË¶ÅÊµèËßàÂô®
      if (ua.includes("edg")) {
        browserName = "Microsoft Edge";
        browserVersion = this.extractVersion(ua, /edg\/([\d.]+)/);
      } else if (ua.includes("chrome") && !ua.includes("edge")) {
        browserName = "Google Chrome";
        browserVersion = this.extractVersion(ua, /chrome\/([\d.]+)/);
      } else if (ua.includes("firefox")) {
        browserName = "Mozilla Firefox";
        browserVersion = this.extractVersion(ua, /firefox\/([\d.]+)/);
      } else if (ua.includes("safari") && !ua.includes("chrome")) {
        browserName = "Safari";
        browserVersion = this.extractVersion(ua, /version\/([\d.]+)/);
      } else if (ua.includes("msie") || ua.includes("trident")) {
        browserName = "Internet Explorer";
        browserVersion = this.extractVersion(ua, /(msie |rv:)([\d.]+)/);
        isCompatible = false;
      } else if (ua.includes("360se") || ua.includes("qihu")) {
        browserName = "360ÊµèËßàÂô®";
        isCompatible = false;
      }

      this.browserInfo = {
        name: browserName,
        version: browserVersion,
        isCompatible: isCompatible,
        showWarning: !isCompatible,
      };

      if (!isCompatible) {
        console.error(`${browserName}ÊµèËßàÂô®‰∏çÊîØÊåÅÔºåËØ∑‰ΩøÁî®Áé∞‰ª£ÊµèËßàÂô®`);
      }
    },

    /**
     * ÊèêÂèñÊµèËßàÂô®ÁâàÊú¨Âè∑
     */
    extractVersion(ua, regex) {
      const match = ua.match(regex);
      return match ? match[1] : "Êú™Áü•ÁâàÊú¨";
    },

    /**
     * Ëß¶ÂèëÊñá‰ª∂ÈÄâÊã©ÂØπËØùÊ°Ü
     */
    triggerFileInput() {
      this.$refs.fileInput.click();
    },

    /**
     * Â§ÑÁêÜÊñá‰ª∂ÈÄâÊã©‰∫ã‰ª∂
     */
    handleFileSelect(event) {
      const file = event.target.files[0];
      if (file) {
        this.processSelectedFile(file);
      }
      // ÈáçÁΩÆinputÔºåÂÖÅËÆ∏ÈÄâÊã©Âêå‰∏Ä‰∏™Êñá‰ª∂
      event.target.value = "";
    },

    /**
     * Â§ÑÁêÜÊãñÊãΩËøõÂÖ•‰∫ã‰ª∂
     */
    handleDragOver() {
      this.isDragging = true;
    },

    /**
     * Â§ÑÁêÜÊãñÊãΩÁ¶ªÂºÄ‰∫ã‰ª∂
     */
    handleDragLeave() {
      this.isDragging = false;
    },

    /**
     * Â§ÑÁêÜÊñá‰ª∂ÊãñÊîæ‰∫ã‰ª∂
     */
    handleDrop(event) {
      this.isDragging = false;
      const file = event.dataTransfer.files[0];
      if (file) {
        this.processSelectedFile(file);
      }
    },

    /**
     * Â§ÑÁêÜÈÄâÊã©ÁöÑÊñá‰ª∂
     */
    processSelectedFile(file) {
      // È™åËØÅÊñá‰ª∂
      if (!this.validateFile(file)) {
        return;
      }

      this.currentFile = file;
      this.clearError();
      this.parseStatus = 'Êñá‰ª∂Â∑≤ÂáÜÂ§áÂ∞±Áª™ÔºåÁÇπÂáª"Ëß£ÊûêÊñá‰ª∂"ÂºÄÂßãËß£Êûê';

      // ÊòæÁ§∫Êñá‰ª∂‰ø°ÊÅØ
      this.$message.success(`Â∑≤ÈÄâÊã©Êñá‰ª∂: ${file.name}`);
    },

    /**
     * È™åËØÅÊñá‰ª∂Ê†ºÂºèÂíåÂ§ßÂ∞è
     */
    validateFile(file) {
      // Ê£ÄÊü•Êñá‰ª∂Á±ªÂûã
      const validExtensions = [".csv", ".txt"];
      const fileExtension = file.name
        .toLowerCase()
        .substring(file.name.lastIndexOf("."));

      if (!validExtensions.includes(fileExtension)) {
        this.showError("ËØ∑ÈÄâÊã©CSVÊàñÊñáÊú¨Êñá‰ª∂ (.csv, .txt)", "Êñá‰ª∂Á±ªÂûãÈîôËØØ");
        return false;
      }

      // Ê£ÄÊü•Êñá‰ª∂Â§ßÂ∞èÔºà10MBÈôêÂà∂Ôºâ
      const maxSize = 10 * 1024 * 1024;
      if (file.size > maxSize) {
        this.showError(
          `Êñá‰ª∂Â§ßÂ∞è‰∏çËÉΩË∂ÖËøá10MBÔºåÂΩìÂâçÊñá‰ª∂Â§ßÂ∞è: ${(
            file.size /
            1024 /
            1024
          ).toFixed(2)}MB`,
          "Êñá‰ª∂ËøáÂ§ß"
        );
        return false;
      }

      // Ê£ÄÊü•Á©∫Êñá‰ª∂
      if (file.size === 0) {
        this.showError("Êñá‰ª∂‰∏∫Á©∫ÔºåËØ∑ÈÄâÊã©ÊúâÊïàÁöÑCSVÊñá‰ª∂", "Á©∫Êñá‰ª∂ÈîôËØØ");
        return false;
      }

      return true;
    },

    /**
     * ÊòæÁ§∫ÈîôËØØ‰ø°ÊÅØ
     */
    showError(message, type = "ÈîôËØØ") {
      this.parseError = `${type}: ${message}`;
      this.parseErrorDetails = this.getErrorDetails();
      this.errorSolutions = this.getErrorSolutions(type);
      this.$message.error(message);
    },

    /**
     * Ê∏ÖÈô§ÈîôËØØ‰ø°ÊÅØ
     */
    clearError() {
      this.parseError = "";
      this.parseErrorDetails = "";
      this.errorSolutions = [];
    },

    /**
     * Ëé∑ÂèñÈîôËØØËØ¶ÊÉÖ
     */
    getErrorDetails() {
      return `ÈîôËØØÊó∂Èó¥: ${new Date().toLocaleString()}
              Áî®Êà∑‰ª£ÁêÜ: ${this.userAgent}
              Êñá‰ª∂ÂêçÁß∞: ${this.currentFile ? this.currentFile.name : "Êú™Áü•"}
              Êñá‰ª∂Â§ßÂ∞è: ${
                this.currentFile
                  ? (this.currentFile.size / 1024).toFixed(2) + "KB"
                  : "Êú™Áü•"
              }
      ÊµèËßàÂô®‰ø°ÊÅØ: ${this.getBrowserInfo()}`;
    },

    /**
     * Ëé∑ÂèñÊµèËßàÂô®‰ø°ÊÅØ
     */
    getBrowserInfo() {
      const ua = this.userAgent;
      if (ua.includes("Chrome") && ua.includes("Edg")) {
        return "Microsoft Edge";
      } else if (ua.includes("Chrome")) {
        return "Google Chrome";
      } else if (ua.includes("Firefox")) {
        return "Mozilla Firefox";
      } else if (ua.includes("Safari") && !ua.includes("Chrome")) {
        return "Safari";
      } else if (ua.includes("360se") || ua.includes("qihu")) {
        return "360ÊµèËßàÂô®";
      } else if (ua.includes("msie") || ua.includes("trident")) {
        return "Internet Explorer";
      }
      return "Êú™Áü•ÊµèËßàÂô®";
    },

    /**
     * Ëé∑ÂèñÈîôËØØËß£ÂÜ≥ÊñπÊ°à
     */
    getErrorSolutions(errorType) {
      const solutions = [];

      switch (errorType) {
        case "Êñá‰ª∂Á±ªÂûãÈîôËØØ":
          solutions.push("ËØ∑Á°Æ‰øùÊñá‰ª∂Êâ©Â±ïÂêç‰∏∫ .csv Êàñ .txt");
          solutions.push("Ê£ÄÊü•Êñá‰ª∂ÊòØÂê¶ÊçüÂùèÊàñÊ†ºÂºè‰∏çÊ≠£Á°Æ");
          break;

        case "Êñá‰ª∂ËøáÂ§ß":
          solutions.push("ËØ∑ÈÄâÊã©Â∞è‰∫é10MBÁöÑÊñá‰ª∂");
          solutions.push("ÂèØ‰ª•Â∞ÜÂ§ßÊñá‰ª∂ÂàÜÂâ≤ÊàêÂ§ö‰∏™Â∞èÊñá‰ª∂ÂàÜÊâπÂ§ÑÁêÜ");
          break;

        case "Á©∫Êñá‰ª∂ÈîôËØØ":
          solutions.push("ËØ∑ÈÄâÊã©ÈùûÁ©∫Êñá‰ª∂");
          solutions.push("Ê£ÄÊü•Êñá‰ª∂ÂÜÖÂÆπÊòØÂê¶Ë¢´Ê∏ÖÁ©∫");
          break;

        case "ÁºñÁ†ÅÈîôËØØ":
          solutions.push("Â∞ùËØïÂ∞ÜÊñá‰ª∂Âè¶Â≠ò‰∏∫UTF-8ÁºñÁ†ÅÊ†ºÂºè");
          solutions.push("‰ΩøÁî®ÊñáÊú¨ÁºñËæëÂô®Ê£ÄÊü•Êñá‰ª∂ÁºñÁ†Å");
          solutions.push("ÈÅøÂÖç‰ΩøÁî®ÁâπÊÆäÂ≠óÁ¨¶ÊàñBOMÂ§¥");
          break;

        case "Ëß£ÊûêÈîôËØØ":
          solutions.push("Ê£ÄÊü•CSVÊñá‰ª∂Ê†ºÂºèÊòØÂê¶Ê≠£Á°Æ");
          solutions.push("Á°Æ‰øùÂàÜÈöîÁ¨¶‰ΩøÁî®‰∏ÄËá¥ÔºàÂª∫ËÆÆ‰ΩøÁî®ÈÄóÂè∑Ôºâ");
          solutions.push("Ê£ÄÊü•ÊòØÂê¶ÊúâÊú™Èó≠ÂêàÁöÑÂºïÂè∑");
          solutions.push("Â∞ùËØï‰ΩøÁî®ÂÖ∂‰ªñÊµèËßàÂô®ÔºàÂ¶ÇChrome„ÄÅFirefoxÔºâ");
          break;

        default:
          solutions.push("Â∞ùËØïÈáçÊñ∞ÈÄâÊã©Êñá‰ª∂");
          solutions.push("Ê£ÄÊü•Êñá‰ª∂ÊòØÂê¶ÊçüÂùè");
          solutions.push("Â∞ùËØï‰ΩøÁî®ÂÖ∂‰ªñÊµèËßàÂô®");
          solutions.push("ËÅîÁ≥ªÊäÄÊúØÊîØÊåÅÊèê‰æõÈîôËØØËØ¶ÊÉÖ");
      }

      return solutions;
    },

    /**
     * Ëß£ÊûêCSVÊñá‰ª∂
     */
    async parseCSVFile() {
      if (!this.currentFile) {
        this.$message.warning("ËØ∑ÂÖàÈÄâÊã©Êñá‰ª∂");
        return;
      }

      // ÈáçÁΩÆÁä∂ÊÄÅ
      this.isParsing = true;
      this.clearError();
      this.parseStatus = "ÂºÄÂßãËß£ÊûêÊñá‰ª∂...";

      try {
        // ‰ΩøÁî®FileReaderËØªÂèñÊñá‰ª∂
        const csvText = await this.readFileWithFallback(this.currentFile);

        if (!csvText) {
          throw new Error("Êñá‰ª∂ËØªÂèñÂ§±Ë¥•ÔºåÂèØËÉΩ‰∏∫Á©∫ÊàñÁºñÁ†Å‰∏çÊ≠£Á°Æ");
        }

        this.parseStatus = "Ê≠£Âú®Ëß£ÊûêÊï∞ÊçÆ...";

        // ‰ΩøÁî®PapaParseËß£ÊûêCSV
        const results = await this.parseWithPapaParse(csvText);

        // Â§ÑÁêÜËß£ÊûêÁªìÊûú
        this.handleParseResults(results);
      } catch (error) {
        this.handleParseError(error);
      } finally {
        // Âª∂ËøüÂÖ≥Èó≠Ëß£ÊûêÁä∂ÊÄÅÔºåËÆ©Áî®Êà∑ÁúãÂà∞ÂÆåÊàêÁä∂ÊÄÅ
        setTimeout(() => {
          this.isParsing = false;
        }, 800);
      }
    },

    /**
     * ËØªÂèñÊñá‰ª∂
     */
    readFileWithFallback(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();

        reader.onload = (event) => {
          try {
            let content = event.target.result;

            // Â§ÑÁêÜBOMÂ§¥
            if (content.charCodeAt(0) === 0xfeff) {
              content = content.slice(1);
            }

            resolve(content);
          } catch (error) {
            reject(new Error(`Êñá‰ª∂ËØªÂèñÈîôËØØ: ${error.message}`));
          }
        };

        reader.onerror = () => {
          reject(new Error("Êñá‰ª∂ËØªÂèñÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•Êñá‰ª∂ÊùÉÈôêÊàñÊ†ºÂºè"));
        };

        reader.onabort = () => {
          reject(new Error("Êñá‰ª∂ËØªÂèñË¢´‰∏≠Ê≠¢"));
        };

        // ËÆæÁΩÆË∂ÖÊó∂
        const timeout = setTimeout(() => {
          reader.abort();
          reject(new Error("Êñá‰ª∂ËØªÂèñË∂ÖÊó∂"));
        }, 30000);

        reader.onloadend = () => {
          clearTimeout(timeout);
        };

        // Â∞ùËØï‰∏çÂêåÁöÑËØªÂèñÊñπÂºè
        try {
          reader.readAsText(file, "UTF-8");
        } catch (error) {
          try {
            reader.readAsText(file);
          } catch (fallbackError) {
            reject(new Error(`Êó†Ê≥ïËØªÂèñÊñá‰ª∂: ${fallbackError.message}`));
          }
        }
      });
    },

    /**
     * ‰ΩøÁî®PapaParseËß£Êûê
     */
    parseWithPapaParse(csvText) {
      return new Promise((resolve, reject) => {
        try {
          Papa.parse(csvText, {
            ...this.parseConfig,
            complete: (results) => {
              resolve(results);
            },
            error: (error) => {
              reject(new Error(`CSVËß£ÊûêÈîôËØØ: ${error.message}`));
            },
          });
        } catch (error) {
          reject(new Error(`Ëß£ÊûêËøáÁ®ãÂºÇÂ∏∏: ${error.message}`));
        }
      });
    },

    /**
     * Â§ÑÁêÜËß£ÊûêÁªìÊûú
     */
    handleParseResults(results) {
      if (!results || !results.data) {
        throw new Error("Ëß£ÊûêÁªìÊûú‰∏∫Á©∫");
      }

      const rawData = results.data;

      if (rawData.length === 0) {
        throw new Error("CSVÊñá‰ª∂ÂÜÖÂÆπ‰∏∫Á©∫");
      }

      // Ëé∑ÂèñË°®Â§¥
      let headers = [];
      if (this.parseConfig.header && results.meta && results.meta.fields) {
        headers = results.meta.fields;
      } else if (rawData[0]) {
        const firstRow = rawData[0];
        if (firstRow && typeof firstRow === "object") {
          headers = Object.keys(firstRow).map(function (_, i) {
            return "Âàó" + (i + 1);
          });
        }
      }

      // ËøáÊª§Êó†ÊïàÊï∞ÊçÆ
      const validData = rawData.filter(function (row) {
        if (!row || Object.keys(row).length === 0) {
          return false;
        }

        return Object.values(row).some(function (value) {
          return (
            value != null &&
            value !== "" &&
            !(typeof value === "number" && isNaN(value))
          );
        });
      });

      if (validData.length === 0) {
        throw new Error("Ê≤°ÊúâÊúâÊïàÁöÑÊï∞ÊçÆË°å");
      }

      // Ê∑ªÂä†Ëß£ÊûêÊàêÂäüÊèêÁ§∫
      this.$message.success(`Ëß£ÊûêÊàêÂäüÔºå${validData.length} Ë°åÊï∞ÊçÆ`);

      // ÂèëÈÄÅËß£ÊûêÂÆåÊàê‰∫ã‰ª∂
      this.$emit("data-loaded", {
        data: validData,
        headers: headers,
      });

      this.parseStatus = "Ëß£ÊûêÂÆåÊàêÔºÅ";

      // Ëá™Âä®ÈáçÁΩÆÁä∂ÊÄÅ
      setTimeout(() => {
        this.parseStatus = "";
      }, 2000);
    },

    /**
     * Â§ÑÁêÜËß£ÊûêÈîôËØØ
     */
    handleParseError(error) {
      let errorMessage = error.message || "Êú™Áü•ÈîôËØØ";
      let errorType = "Ëß£ÊûêÈîôËØØ";

      this.parseError = `${errorType}: ${errorMessage}`;
      this.parseErrorDetails = this.getErrorDetails();
      this.errorSolutions = this.getErrorSolutions(errorType);

      console.error("CSVËß£ÊûêÂ§±Ë¥•:", error);
      this.$message.error(`Ëß£ÊûêÂ§±Ë¥•: ${errorMessage}`);
    },

    /**
     * Ê∏ÖÈô§Êñá‰ª∂
     */
    clearFile() {
      this.currentFile = null;
      this.isParsing = false;
      this.parseStatus = "";
      this.clearError();
      this.$refs.fileInput.value = "";
    },
  },
};
</script>

